<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix" align="center">
      <span>金属卟啉仿生催化甲苯液相氧化连续实验</span>
    </div>
    <div>
      <el-image
        style="width: 100%;height: 100%"
        :src = "require('../assets/process_1.png')"
        fit = "scale-down"
      ></el-image>
    </div>
    <div class="text item" align="center">图 1甲苯仿生催化氧化三釜串联反应装置示意图</div>
    <div>
      甲苯连续氧化反应在图所示的三釜串联连续反应装置中进行。由三个串联的钛材反应釜组成。实验过程如下：配制含催化剂甲苯溶液加入到带有氮气保护的缓冲储罐V1中，将配好甲苯反应液加入到3L高压钛材反应R1釜中，依次流入3L高压反应R2、R3釜。待反应物充满后，反应系统通入高纯氮气，升温到设定温度后经气体质量流量计定量通入空气并反应，三路自动测氧仪跟踪检测反应进程。定时取样分析，实验过程中当尾气中含氧浓度恒定后再取样三次检测，产物稳定后再变化反应操作条件。取出的样品首先在密闭的带有水冷的取样管中冷却再转移到锥形瓶中储存于冰箱中。
    </div>
    <div align="center">甲苯仿生催化连续氧化反应装置</div>
    <div>
      <el-image
        style="width: 100%;height: 100%"
        :src = "require('../assets/process_2.png')"
        fit = "scale-down"
      ></el-image>
    </div>
    <div class="text item" align="center">图 2甲苯仿生催化氧化连续反应工艺流程图</div>
    <div>自行设计的甲苯仿生催化连续氧化装置工艺流程图见图，本装置由三釜串联反应系统、反应气液进料系统、反应温度控制系统组成反应系统由三个容积为 3L 钛材反应釜一，反应釜二、三串联组成，每釜分别含锚式和推进式两种形式的搅拌浆，用于防止物料的沉积并保证物料的充分混合。各反应釜分别拥有独立的搅拌系统，可通过各自变频调速器控制不同的转速，搅拌采用电磁密封式，用高温磁铁作为传导轴，并将磁铁部分放置在导热油加热箱外。三个反应釜采用三套独立的PID智能控制系统控制反应釜的温度，每个反应釜的智能温控系统通过调冷热导热油电磁阀的开闭完成对温度智能调节控制，温度控制范围在±1℃。
      反应气液进料系统由气体进料和液体进料两部分组成，气体进料包括设备由无油螺杆空气压缩机、脱水器、气体质量控制流量计、环型气体分布器等设备组成。压缩的饱和空气经脱水器分子筛脱水后通过各釜质量流量控制器计量流量后再经气体预热器加热到设定温度后，通过气体环型分布器与反应液反应；液体进料系统包括高效液相色谱专用计量泵、液体预热系统。反应液甲苯，经高效液相色谱专用泵计量增压后，经液体预热器加热后进入到各个反应釜与空气反应。
      反应温度控制等系统主要由导热油加热系统和导热油冷却系统组成，主要设备包含导热油储罐，热油泵，冷油泵，导热热油电磁阀，导热油冷油电磁阀、控制仪表等设备组成。当某釜反应温度低于设定温度，热油泵启动同时相应反应釜，导热油热油电磁阀打开通过反应釜夹套给反应釜加热，当某一反应釜温度高于设定温度，导热油热油电磁阀关闭，冷油泵启动，导热油冷油电磁阀启动冷油进入反应釜蛇形冷却盘管反应釜降温。
    </div>
  </el-card>
</template>

<script>
export default {
  name: "process"
}
</script>

<style scoped>

</style>



